import { ImageData } from '@ali/4ever-mo';
import { pick, isEqual } from 'lodash-es';

export function isStyleActive(styleData: ImageData, imageData: ImageData | null) {
  const styleKeys = ['outline', 'radius', 'shadow'];
  const pickedStyleData = pick(styleData, styleKeys);
  const pickedImageData = pick(imageData, styleKeys);
  return isEqual(pickedStyleData, pickedImageData);
}
